@use 'base/variables' as v;
@use 'base/mixins' as m;

/* MAIN */ 

.main-contacto {
    width: 100%;
    margin: 0 auto;
    display: grid;
    place-content: center;
    height: 80%;
    @include m.tablet {
        width: 80%;
    }
    .contacto {
        padding: 3rem;
        width: 100%;
        position: relative;
        height: 100%;
        display: flex;
        flex-direction: column;
        text-align: center;
        &::after , &::before {
            content: '';
            position: absolute;
            width: 70%;
            pointer-events: none;
            background: transparent;
            height: 60%;
            border: 10px solid v.$rosado;
        }
        &::after {
                top: 0;
                left: 0;
                border-bottom: none;
                border-right: none;
        }
        &::before {
            right: 0;
            bottom: 0;
            border-top: none;
            border-left: none;
        }
    }
}
.contacto {
    h2 {
        font-size: 10vw;
        color: v.$blanco;
        @include m.tablet {
            font-size: 6.5vw;
        }
    }
    .texto__contacto {
        color: v.$blanco;
    font-family: v.$texto;
    font-size: 2rem;
    line-height: 50px;

    a {
        color: v.$blanco;
        padding: 10px;
        border: 1px solid v.$rosado;
        text-decoration-line: none;
        transition: all .5s ease-in-out;
        width: 100%;
        &:hover {
            color: v.$cafe;
            box-shadow: 0 60px inset v.$rosado;
        }
        @include m.telefono {
            width: 300px;
        }
    }
    }
}