@use 'base/variables' as v;
@use 'base/mixins' as m;
.home {
    position: absolute;
    top: -20px;
    right: 5rem;
    color: v.$blanco;
    text-transform: uppercase;
    text-decoration-line:line-through;
    transition: .2s ease-in-out;
    font-size: 2rem;
    @include m.tablet {
        font-size: 1.5rem;
        right: 1.2rem;
        transform: rotate(90deg);
    }

    &:hover {
        transform: rotate(90deg) scale(1.1);
        color: v.$cafe;
    }
}