@use 'base/variables' as  v;
@use 'base/mixins' as m;

.main-informacion {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
    padding: 2rem;
    justify-content: center;
    align-items: center;
    margin: 50px auto;
    .home {
        color: v.$negroClaro;
    }
    .informacion {
            display: grid;
            place-items: center;
            grid-template-columns: repeat(1, 1fr);
            width: 100%;
            margin-top: 60px;
            min-height: 380px;
            box-shadow: 0 0 60px rgba(0, 0, 0, 0.267);
            background-color: darken(v.$rosado,10%);
            transition: .3s ease-in-out;

            &:hover {
                transform: scale(1.06);
            }
            @include m.desktop {
                grid-template-columns: 55% 45%;
                border-radius: 0 0 56% 0;
                width: 80%;
            }
    }
}
.CV {
    margin-top: 10rem;
    position: relative;
    font-family: v.$texto;
    padding: 2rem;
    width: 150px;
    text-align: center;
    color: v.$negroClaro;
    text-decoration-line: none;
    font-weight: 900;
    font-size: 2.5rem;
    &::after , &::before {
        content: '';
        position: absolute;
        width: 30px;
        height: 30px;
        border: 4px solid v.$negroClaro;
        transition: all .4s ease-in-out;
    }
    &::after {
        top: 0;
        left: 0;
        border-right:none ;
        border-bottom: none;
    }
    &::before {
        bottom: 0;
        right: 0;
        border-left: none;
        border-top: none;
    }
    &:hover::after , &:hover::before {
        width: 99%;
        height: 99%;
    }
}
.informacion {
    img {
        width: 40%;
        height: auto;
        grid-row: 1;
        border-radius: 50%;

        @include m.desktop {
            width: 100%;
            min-height: 380px;
            border-radius: 0 0 60% 0;
            grid-column: 2;
        }
    }
    .informacion-texto {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        padding: 3rem;
        align-items: center;
        text-align: center;
        h2 {
            font-size: 7vw;
            @include m.desktop {
                font-size: 4vw;
            }
        }
        p {
            font-family: v.$texto;
            font-size: 1.5rem;
            font-weight: 700;
            line-height: 30px;
            letter-spacing: 5px;
        }
    }
}