@use 'base/variables' as v;
@use 'base/mixins' as m;
.cursor{
    display: none;
    @include m.telefono {
        width: 45px;
        height: 45px;
        position: fixed;
        background: transparent;
        border-radius: 50%;
        border: 1px solid v.$blanco;
        pointer-events: none;
    }
}