@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
    width: 95%;
    gap: 1rem;
    bottom: -70px;
    @include m.desktop {
        bottom: 0;
    }
    i {
        font-size: 2rem;
        color: v.$negroClaro;
    }
    p {
        font-family: v.$texto;
        color: v.$negroClaro;
        font-size: 2rem;
    }
}