@use 'base/variables' as v;

.animacionHome {
    width: 100%;
    height: 0;
    z-index: 999;
    background-color: v.$negroClaro;
    position: absolute;
    top: 0;
    left: 0;
    animation: home 1.2s ease-in;
    @keyframes home {
        0% {
            height: 100%;
        }
        100% {
            height: 0;
        }
    }
}
.animacionContacto {
    width: 100%;
    height: 0;
    z-index: 999;
    background-color: v.$rosado;
    position: absolute;
    bottom: 0;
    left: 0;
    animation: contacto 1.2s ease-in;
    @keyframes contacto {
        0% {
            height: 100%;
        }
        100% {
            height: 0;
        }
    }
}
.animacionInformacion {
    width: 0;
    height: 500%;
    left: 0;
    position: absolute;
    top: 0;
    z-index: 999;
    background-color: v.$negroClaro;
    animation: info 1.2s ease-in;
    @keyframes info {
        0% {
            width: 100%;
        }
        100% {
            width: 0;
        }
    }
}
.animacionProyectos {
    width: 0;
    min-height: 500%;
    position: absolute;
    top: 0;
    right: 0;
    background-color: v.$rosado;
    z-index: 999;
    animation: proyectos 1.2s ease-in;

    @keyframes proyectos {
        0% {
            width: 100%;
        }
        100% {
            width: 0;
        }
    }
}