@use 'base/variables' as v;
@use 'base/mixins' as m;
.header {
    display: flex;
    justify-content: flex-start;
    height: auto;
    line-height: 100px;
    align-items: center;
    width: 95%;

    .navegacion {
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        @include m.telefono {
            width: 250px;
            margin-left: 6rem;
        }
    }
}

.navegacion {

    a {
        position: relative;
        text-transform: uppercase;
        color: v.$negroClaro;
        font-size: 2.8rem;
        transition: all .3s;
        position: relative;
        display: inline-block;
        transition: all .2s ease-in-out;
        &:hover {
            transform: scale(1.12);
            color: v.$cafe;
        }
    }
}
