@use 'base/variables' as v;
@use 'base/mixins' as m;

/* MAIN */

.main-home {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 30px 30px 0 10px;
        gap: 7rem;
        margin-top: 20px;
    @include m.desktop {
        flex-direction: row;
        height: 80%;
        margin: 0 auto;
        justify-content: space-around;
        align-items: center;
    }
        .contenedor_texto {
                width: 100%;
                padding: 20px;
                border-bottom: 2px solid v.$negroClaro;
                @include m.desktop {
                    width: 30%;
                    border-right: 2px solid v.$negroClaro;
                    border-bottom: none;
                    margin: 200px 0 0 0;
                }
        }
        .botones {
            width: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            gap: 3rem;
            @include m.desktop {
                width: 55%;
            }
        }
}

.contenedor_texto {
    .titulo {
        font-size: 3vw;
        text-align: center;
        @include m.desktop {
            text-align: left;
            font-size: 2rem;
        }
        
    }
    p {
        text-align: center;
        font-family: v.$texto;
        text-decoration-line: underline;
        font-size: 3vw;
        line-height: 50px;
        letter-spacing: 5px;
        @include m.desktop {
            font-size: 1.5rem;
        }
    }
}
.botones {
    .boton {
        text-decoration: none;
        color: v.$negroClaro;
        background: transparent;
        font-family: v.$titulo;
        font-size: 10vw;
        @include m.desktop {
            font-size: 8.5vw;
        }
        &:hover {
            font-style: italic;
            color: #474545;
            cursor: pointer;
        }
    }
}