@use 'base/variables' as v;
@use 'base/mixins' as m;


.main-proyectos {
    width: 100%;
    position: relative;
    top: 150px;
    @include m.tablet {
        top: 100px;
    }
    @include m.desktop {
        top: 130px;
        height: auto;
    }
    .contenedor {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        gap: 5rem;
        align-items: center;
        @include m.desktop {
            flex-direction: row;
        }
    }
    .conteiner {
        width: 100%;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        padding: 3rem;
        position: relative;
    }
}
.contenedor {
    .texto-proyectos {
            width: 100%;
            padding: 3rem;
            text-align: center;
            color: v.$blanco;
            font-family: v.$texto;
            text-decoration-line: underline;
            line-height: 40px;
            font-size: 2rem;
            border-bottom: 2px solid v.$rosado;
            @include m.tablet {
                font-size: 3.2rem;
                line-height: 70px;
            }
            @include m.desktop {
                width: 45%;
                font-size: 2rem;
                border-bottom: none;
                border-right: 2px solid v.$rosado;
                line-height: 60px;
            }
    }
    .proyectos {
        display: flex;
        width: 100%;
        flex-direction: column;
        overflow: hidden;
        overflow-y: scroll;
        align-items: center;
        gap: 5rem;
        height: 270px;
        @include m.tablet {
            height: 400px;
        }
        @include m.desktop {
            height: 500px;
        }
    }
}
.proyectos {
    &::-webkit-scrollbar{
        width: 12px;
        border-radius: 15px;
        background-color: rgba(54, 52, 52, 0.863);
    }
    .proyecto {
        background: transparent;
        font-family: v.$titulo;
        color: v.$blanco;
        border: none;
        font-size: 12vw;
        &:hover {
            color: v.$cafe;
            font-style: italic;
        }
        @include m.desktop {
            font-size: 7vw;
        }
    }
}

.conteiner {
    color: v.$blanco;
   h2 {
       font-size: 10vw;
       font-family: v.$titulo;
       border-bottom: 2px solid v.$rosado;
       padding: 3rem;
       @include m.desktop {
            font-size: 5.5vw;
        }
   }
   img {
       height: auto;
        width: 100%;
       box-shadow: 0 0 65px rgba(0, 0, 0, 0.884);
       @include m.tablet {
           width: 85%;
       }
   }
   p {
       font-size: 4vw;
       letter-spacing: 5px;
       font-family: v.$texto;
       border-top: 2px solid v.$rosado;
       line-height: 30px;
       padding: 3rem 0;
       @include m.telefono {
        font-size: 5vw;
        line-height: 50px;
        letter-spacing: 7px;
        padding: 3rem;
       }
       @include m.desktop {
           font-size: 1.8vw;
           line-height: 65px;

        }
   }
   .ver-pagina {
        text-decoration-line: none;
        font-size: 2.5rem;
        font-family: v.$titulo;
        color: v.$rosado;
        text-align: center;
        border-radius: 10px;
        border: 1px solid v.$rosado;
        display: grid;
        place-content: center;
        padding: 2rem;
        text-transform: uppercase;
        transition: .6s ease-in-out;

        &:hover {
            color: v.$negroClaro;
            box-shadow: inset 8.5em 0 0 0  v.$rosado;
        }
   }
}
.volver {
    position: absolute;
    top: -20px;
    left: 15px;
    font-size: 2rem;
    background: transparent;
    border: none;
    color: v.$blanco;
    font-size: 2rem;
    font-family: v.$titulo;
    text-transform: uppercase;
    text-decoration-line: line-through;
    transition: all .2s ease-in-out;
    @include m.tablet {
        transform: rotate(90deg);
        font-size: 1.5rem;
    }
    &:hover {
            transform: rotate(90deg) scale(1.1);
    }
}